import React from 'react'
import {Nav as BsNav,Navbar as BsNavbar,NavDropdown as BsNavDropdown} from 'react-bootstrap'
import {Form,FormControl,Button} from 'react-bootstrap'

import 'bootstrap/dist/css/bootstrap.min.css';

const NavbarBs = () => {
    // const [show, toggleShow] = useState(true);
    return (
        <>
            <BsNavbar bg="light" expand="lg">
                <BsNavbar.Brand href="#home">React-Bootstrap</BsNavbar.Brand>
                <BsNavbar.Toggle aria-controls="basic-navbar-nav" />
                <BsNavbar.Collapse id="basic-navbar-nav">
                    <BsNav className="mr-auto">
                        <BsNav.Link href="/">Home</BsNav.Link>
                        <BsNav.Link href="/contact/1">Contact</BsNav.Link>
                        <BsNav.Link href="/products/1">Product</BsNav.Link>
                        <BsNav.Link href="/about">About</BsNav.Link>
                        <BsNavDropdown title="Dropdown" id="basic-nav-dropdown">
                            <BsNavDropdown.Item href="#action/3.1">Action</BsNavDropdown.Item>
                            <BsNavDropdown.Item href="#action/3.2">Another action</BsNavDropdown.Item>
                            <BsNavDropdown.Item href="#action/3.3">Something</BsNavDropdown.Item>
                            <BsNavDropdown.Divider />
                            <BsNavDropdown.Item href="#action/3.4">Separated link</BsNavDropdown.Item>
                        </BsNavDropdown>
                    </BsNav>
                    <Form inline>
                        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                </BsNavbar.Collapse>
            </BsNavbar>  
        </>
    );      
}

export default NavbarBs
