// Product.js

import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import NavbarBs from '../navbar';
import StarRatings from 'react-star-ratings';
import { SERVER_ROOT_URL } from '../../App'

class Product extends Component {
    constructor(props) {
        super(props);
    
        this.state = { redirect: null, rating: 0, products:[] };
    
        this.changeRating = this.changeRating.bind(this);
    }
    
    componentDidMount() {
        var serverRootUrl = SERVER_ROOT_URL;
        
        fetch(`${serverRootUrl}/products`)
          .then(res => {
              console.log(res);
              return res.json()
           })
          .then(data => { 
              console.log(data.products.join(',')); 
              this.setState({ products : data.products })
          });
      }

    changeRating( newRating ) {
        this.setState({
          rating: newRating
        });
    }

    render() {
        var productId = this.props.match.params.prodId;
        if (isNaN(productId))
            this.setState({ redirect: "/notfound" });
        
        if (this.state.redirect)
            return(
                <Redirect to = {this.state.redirect}></Redirect>
            )
        else
            return (
                <div>
                    <NavbarBs/>
                    <div>
                        <h2>Welcome to "Product" page (page# = {productId})</h2>
                    </div>
                    <StarRatings
                        rating={this.state.rating}
                        starRatedColor="blue"
                        changeRating={this.changeRating}
                        numberOfStars={6}
                        name='rating'
                    />
                    <h2>Products</h2>
                        {this.state.products.map(product =>
                        <div>Product: {product}</div>
                    )}
                </div>
            );
    }
}

export default Product;
