// Contact.js

import {React, Component} from 'react';
import { useParams } from 'react-router-dom';
import NavbarBs from '../navbar';
import { SERVER_ROOT_URL } from '../../App'

class Contact extends Component {
    constructor(){
        super();
        this.state ={data: null};
    }

    componentDidMount() {
        var serverRootUrl = SERVER_ROOT_URL;
        fetch(`${serverRootUrl}/users/image/${this.props.match.params.pageNo}`)
            .then(res => {
                console.log(res);
                return res.json()
            })
            .then(data => { 
                console.log(data.image); 
                this.setState({ data })
            }
        );
    }

    render(){
        var pageNo = this.props.match.params.pageNo; 
        var message = ``;
        if (parseInt(pageNo) > 5) {
            // return <Redirect to="/" />
            message = `Page number outside accepted range`
            return (
                <div>
                    <h3>{message}</h3>
                </div>
            )
        }
        else {
            return ( 
                <div>
                    <NavbarBs/>
                    <div>
                        <h2>Welcome to "Cape Breton Island" page (page# {pageNo})</h2>
                    </div>
                    <div>
                        <img src={`${this.state.data ? this.state.data.image : 'Loading, please wait'}`} />
                    </div>
                </div>
            )
        }    
    }
}

export default Contact;
