// Home.js

import React, { Component } from 'react';
import NavbarBs from '../navbar'
import { SERVER_ROOT_URL } from '../../App'

class Home extends Component {
  constructor(){
    super();
    this.state ={users: null,paintings: []};
  }
  componentDidMount() {
    var serverRootUrl = SERVER_ROOT_URL;
    fetch(`${serverRootUrl}/users`)
      .then(res => {
          console.log(res);
          return res.json()
       })
      .then(users => { 
          console.log(users.team); 
          this.setState({ users })
      });
    fetch(`${serverRootUrl}/products/paintings/all`)
      .then(res => {
          console.log(res);
          return res.json()
       })
      .then(paintingslist => { 
          console.log(paintingslist.data); 
          this.setState({ paintings:paintingslist.data })
      });
  }
  render() {
    return (
      <div>
          <NavbarBs/>
          <h2>Welcome home!</h2>
          <p><b>Users:{this.state.users ? this.state.users.team : 'Loading, please wait...'}</b></p>
          <b/>
          <p><b>Paintings:{this.state.paintings[0] ? `${this.state.paintings[0].name}...${this.state.paintings[0].description}` : 'Loading, please wait...'}</b></p>
          <b/>
          <div>
              Goto "contact" page:&nbsp;&nbsp;
              <a href="/contact/1"><button type="button">contact-1</button></a>
              <a href="/contact/2"><button type="button">contact-2</button></a>
              <a href="/contact/3"><button type="button">contact-3</button></a>
            </div>
            <div>
              Goto "product" page:&nbsp;&nbsp;
              <a href="/products/101"><button type="button">product-101</button></a>
              <a href="/products/102"><button type="button">product-102</button></a>
              <a href="/products/103"><button type="button">product-103</button></a>
            </div>
      </div>
    );
  }
}

export default Home;
