// Notfound.js

import React, { Component } from 'react';
import Navbar from '../navbar';

class Notfound extends Component {
    render() {
        return (
            <div>
                <Navbar/>
                <div>
                    <h3>Page not found!</h3>
                </div>
            </div>
        );
    }
}

export default Notfound;
