// About.js

import React, { useState } from 'react';
import NavbarBs from '../navbar';

export function About() {
  const [readMore,setReadMore]=useState(false);
  const extraContent=<div>
      <p className="extra-content">
        Qui, consectetur nequeab porro quasi culpa nulla rerum quis minus voluptatibus sed hic ad quo sint, libero 
        commodi officia aliquam! Maxime.
      </p>
  </div>
  const linkName=readMore?'Read Less << ':'Read More >> '
  
  return (
    <div>
      <NavbarBs/>
      <div>
        <h2>Welcome to "About" page!</h2>
        <p className="extra-content">Lorem ipsum dolor sit amet consectetur adipisicing elit.{readMore && extraContent}</p>
        <a className="read-more-link" 
          onClick={()=>{setReadMore(!readMore)}}><h2>{linkName}</h2>
        </a>
        
      </div>
    </div>
  );
  
}

export default About;
