// App.js

import './App.css';
import React, { Component } from 'react';
import { BrowserRouter as Router,Switch, Route } from 'react-router-dom';
import Home from './components/home';
import About from './components/about';
import Contact from './components/contact';
import Product from './components/product';
import Notfound from './components/notfound';

export const SERVER_ROOT_URL = process.env.NODE_ENV === 'production' ? process.env.REACT_APP_SERVER_URL : process.env.REACT_APP_SERVER_URL_DEV

class App extends Component {
  render() {
    return (
      <Router>
        <div >
          <Switch>
              <Route exact path='/' component={Home} />
              <Route path='/contact/:pageNo' component={Contact} />
              <Route path='/products/:prodId' component={Product} />
              <Route path='/about' component={About} />
              <Route component={Notfound} />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
